<template>
  <v-container fluid>
    <v-layout
    row wrap
    justify-center
    align-center
    class="mt-5">
      <h1>Thanks for your purchase</h1>
    </v-layout>
    <v-layout
    row wrap
    justify-center
    align-center
    class="my-3">
      <h3>A confirmation receipt will be sent to your checkout email shortly.</h3> 
    </v-layout> 
    <v-layout
    row wrap
    justify-center
    align-center
    class="mb-3">
      <h3>Please notify <font color="green">wearetheparty@blkparty.com</font> if you do not receive it.</h3>
    </v-layout>
    <v-layout
    row wrap
    justify-center
    align-center>
      <v-btn
      color="success"
      @click="returnToShop"
      >
        Continue shopping
      </v-btn>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  mounted () {
    // load all available items
    this.$store.dispatch('itemsApi')
    this.init()
  },
  computed: {
    user () {
      return this.$store.getters.getUser
    },
    vendor () {
      return this.$store.getters.getVendor
    }
  },
  methods: {
    init () {
      // update vendor invoice with paypal payment data
      // 1. get userId and orderId from successful Paypal payment
      const urlParams = new URLSearchParams(window.location.search)
      const data = urlParams.get('d')
      const userId = data.split('/')[0]
      const orderId = data.split('/')[1]
      const payDets = {
        userId: userId,
        orderId: orderId
      }
      // 2. update vendor invoice
      this.$store.dispatch('updateVendorInvoice', payDets)
    },
    returnToShop () {
      if (this.user) {
        // empty cart
        this.$store.dispatch('emptyCart', {userId: this.user.id})
        // load all cart items
        this.$store.dispatch('cartItemsApi', {userId: this.user.id})
      } else if (this.vendor) {
        // empty cart
        this.$store.dispatch('emptyCart', {userId: this.vendor.id})
        // load all cart items
        this.$store.dispatch('cartItemsApi', {userId: this.vendor.id})
      }
      // redirect to shop
      this.$router.push('/shop')
    }
  }
}
</script>



