  // action to send buyer order via paypal
  buyerOrderApi ({commit}, payload) {
    commit('SET_LOADING', true)
    commit('CLEAR_ERROR')
    commit('CLEAR_SUCCESS')
    // set userId
    const userId = payload.userId
    // set payment object
    const order = {
      deliveryEmail: payload.deliveryEmail,
      firstName: payload.firstName,
      lastName: payload.lastName,
      phoneNumber: payload.phoneNumber,
      totalSales: payload.totalSales,
      discountTotal: payload.discountTotal,
      couponDiscount: payload.couponDiscount,
      netSales: payload.netSales,
      orderItems: payload.orderItems,
      paid: payload.paid
    }
    let key
    // create order key first in user's db field
    return firebase.database().ref('users').child(userId).child('orders').push(order)
    .then((data) => {
      key = data.key
      return key
    })
    // retrieve key to store as file property in firebase DB
    .then(key => {
      // update user field with orderId
      firebase.database().ref('users').child(userId).child('orders').child(key).update({
        orderId: key
      })
      // update user field with coupon redeem date (if coupon applied)
      if (payload.couponDiscount !== 0) {
        firebase.database().ref('users').child(userId).child('profile').update({
          couponRegRedeem: payload.couponRegRedeem.toISOString().split('T')[0]
        })
      }
      // stop loading, send orderId and send success msg
      commit('SET_LOADING', false)
      return key
    })
    // store error message
    .catch(error => {
      commit('SET_ERROR', error.message)
      commit('SET_LOADING', false)
    })
  },
  // action to file vendor invoice
  vendorInvoiceApi ({commit}, payload) {
    commit('SET_LOADING', true)
    commit('CLEAR_ERROR')
    commit('CLEAR_SUCCESS')
    const vendorId = payload.vendorId
    const invoice = {
      itemId: payload.itemId,
      itemName: payload.itemName,
      vendorId: vendorId,
      vendorName: payload.vendorName,
      vendorEmail: payload.vendorEmail,
      userId: payload.userId,
      deliveryEmail: payload.deliveryEmail,
      firstName: payload.firstName,
      lastName: payload.lastName,
      fee: payload.fee,
      discountAmt: payload.discountAmt,
      couponDiscountAmt: payload.couponDiscountAmt,
      phoneNumber: payload.phoneNumber,
      dateOrdered: payload.dateOrdered.toLocaleDateString(),
      timeOrdered: payload.dateOrdered.toLocaleTimeString(),
      monthOrdered: payload.dateOrdered.getMonth() + 1,
      expectedDate: payload.expectedDelivery.toLocaleDateString(),
      expectedTime: payload.expectedDelivery.toLocaleTimeString(),
      dateDelivered: payload.dateDelivered,
      timeDelivered:
      payload.dateDelivered,
      priceSGD: payload.priceSGD,
      quantity: payload.quantity,
      totalSales: payload.totalSales,
      netSales: payload.netSales,
      orderId: payload.orderId
    }
    // create order key first in vendor's db field
    firebase.database().ref('users').child(vendorId).child('invoices').child(invoice.orderId).child('invoiceItems').push(invoice)
    .then(() => {
      // stop loading and send success msg
      commit('SET_LOADING', false)
    })
    // store error message
    .catch(error => {
      commit('SET_ERROR', error.message)
      commit('SET_LOADING', false)
    })
  },
  updateVendorInvoice ({commit}, payload) {
    let update
    let vendorIds = []
    // 1. get buyer order details
    firebase.database().ref('users').child(payload.userId).child('orders').child(payload.orderId).once('value')
    .then(data => {
      const obj = data.val()
      // 2. set up invoice update
      update = {
        countryCode: obj.countryCode,
        deliveryAddress: obj.deliveryAddress,
        firstName: obj.firstName,
        lastName: obj.lastName,
        paid: obj.paid,
        paymentDate: obj.paymentDate,
        paymentTime: obj.paymentTime,
        phoneNumber: obj.phoneNumber
      }
      // 3. get all vendorIds in buyer order and remove repeats
      let prevId = null
      for (let i = 0; i < obj.orderItems.length; i++) {
        if (obj.orderItems[i].vendorId !== prevId) {
          // add vendorId to array
          vendorIds.push(obj.orderItems[i].vendorId)
        } else {
          // skip over repeated vendorId
          continue
        }
        prevId = obj.orderItems[i].vendorId
      }
    })
    // 4. update vendor invoice field
    .then(() => {
      for (let j = 0; j < vendorIds.length; j++) {
        firebase.database().ref('users').child(vendorIds[j]).child('invoices').child(payload.orderId).update(update)
      }
    })
    .catch(error => {
      console.log(error)
    })
  },
  // action to get vendor invoices
  vendorInvoicesApi ({commit}, payload) {
    commit('SET_LOADING', true)
    // define required params and outputs to be accessed later
    const vendorId = payload.id
    let invoices = []
    let obj
    // retrieve vendor invoice data
    firebase.database().ref('users').child(vendorId).child('invoices').once('value')
    .then(data => {
      // access values in each invoice
      data.forEach(subData => {
        obj = subData.val()
        // access values in each invoice item
        let items = obj.invoiceItems
        // set invoice object and push to invoices array
        for (let key in items) {
          const invoice = {
            invoiceId: key,
            itemId: items[key].itemId,
            itemName: items[key].itemName,
            vendorId: items[key].vendorId,
            userId: items[key].userId,
            deliveryEmail: items[key].deliveryEmail,
            firstName: items[key].firstName,
            lastName: items[key].lastName,
            fee: items[key].fee,
            discountAmt: items[key].discountAmt,
            couponDiscountAmt: items[key].couponDiscountAmt,
            destination: obj.deliveryAddress.line1,
            zipCode: obj.deliveryAddress.postal_code,
            phoneNumber: items[key].phoneNumber,
            dateOrdered: items[key].dateOrdered,
            timeOrdered: items[key].timeOrdered,
            monthOrdered: items[key].monthOrdered,
            expectedDate: items[key].expectedDate,
            expectedTime: items[key].expectedTime,
            dateDelivered: items[key].dateDelivered,
            timeDelivered: items[key].timeDelivered,
            priceSGD: items[key].priceSGD,
            netSales: items[key].netSales,
            quantity: items[key].quantity,
            cashTransferDate: 'pending update'
          }
          invoices.push(invoice)
        }
      })
      commit('SET_INVOICES', invoices)
      commit('SET_LOADING', false)
    })
    .catch(error => {
      commit('SET_ERROR', error.message)
      commit('SET_LOADING', false)
    })
  },
