<template>
  <v-container fluid>
    <v-flex xs12
    v-if="!buyerAuthenticated">
      <v-layout 
      row wrap
      justify-center
      align-center
      class="mt-5">
        Please login/register to view your shopping cart
      </v-layout>
      <v-layout
      row wrap
      justify-center
      class="mt-4">
        <v-btn
        color="success"
        :to="'/login'">
          Login/Register
        </v-btn>
      </v-layout>
    </v-flex>
    <v-flex xs12
    v-if="buyerAuthenticated">

      <!-- success alert -->
      <div
      class="mt-4"
      v-if="success">
        <app-success-alert 
        @dismissed="onDismissed"
        :text="success"
        class="white--text"></app-success-alert>
      </div>

      <!-- error alert row -->
      <v-layout 
      row
      v-if="error"
      class="mb-2">
        <v-flex xs12>
          <app-error-alert 
          @dismissed="onDismissed"
          :text="error"
          class="black--text"></app-error-alert>
        </v-flex>
      </v-layout>

      <!-- loading circular -->
      <v-layout 
      row wrap 
      v-if="loading">
        <v-flex xs12 class="text-xs-center mt-5">
          <v-progress-circular 
          indeterminate 
          color="green"
          :width="3"
          :size="20"
          ></v-progress-circular>
          Retrieving your cart... 
        </v-flex>
      </v-layout>

      <!-- cart card -->
      <v-layout row wrap
      class="mt-4">
        <h3 class="headline mb-3">Your Shopping Cart</h3>
      </v-layout>
      <v-layout row wrap>
        <v-flex xs12 sm7>  
          <v-card>
            <v-list 
            three-line
            >
              <p 
              v-if="!cartItemsLoaded"
              class="ml-3">
                No items in your cart yet.
              </p>
              <v-list-tile
                v-if="cartItemsLoaded"
                v-for="(cartItem, key) in cartItems"
                :key="key"
                avatar
                class="my-3"
              >
                <v-list-tile-avatar>
                  <img :src="cartItem.imageURL">
                </v-list-tile-avatar>

                <v-layout 
                row wrap
                >
                  <v-flex xs12 sm6 md8 lg9
                  >
                    <v-list-tile-content>
                      <v-list-tile-title>
                        {{cartItem.itemName}}
                      </v-list-tile-title>
                      <v-list-tile-sub-title
                      v-if="cartItem.discount === '0'">
                        <p>
                          SGD {{cartItem.priceSGD}}
                        </p>
                      </v-list-tile-sub-title>
                      <v-list-tile-sub-title
                      v-if="cartItem.discount !== '0'">
                        <p>SGD &nbsp;</p>
                        <s>
                          {{cartItem.priceSGD}}
                        </s>
                        <p>
                          &nbsp; {{cartItem.discountedPrice}} each
                        </p>
                        <span class="success--text font-weight-bold">&nbsp; {{cartItem.discount}}% discount!</span>
                      </v-list-tile-sub-title>
                    </v-list-tile-content>
                  </v-flex>

                  <v-flex xs12 sm6 md4 lg3>
                    <v-list-tile-action
                    justify-space-around>
                      <v-layout row wrap>
                        <v-tooltip
                        bottom>
                          <v-btn
                          slot="activator"
                          icon
                          offset-y
                          @click="removeOneQty(key)">
                            <v-icon
                            color="success">
                              remove
                            </v-icon>
                          </v-btn>
                          <span>Reduce Qty</span>
                        </v-tooltip>
                        <v-avatar
                        class="pb-4">
                          <span
                          class="black--text headline">
                            {{cartItem.qty}}
                          </span>
                        </v-avatar>
                        <v-tooltip
                        bottom>
                          <v-btn
                          slot="activator"
                          icon
                          offset-y
                          @click="addOneQty(key)">
                            <v-icon
                            color="success">
                              add
                            </v-icon>
                          </v-btn>
                        <span>Increase Qty</span> 
                        </v-tooltip>
                        <v-tooltip 
                        bottom>
                          <v-btn
                          slot="activator"
                          icon
                          offset-y
                          @click="removeFromCart(cartItem)">
                            <v-icon
                            color="red lighten-3">clear</v-icon>
                          </v-btn>
                          <span>Remove Item</span>
                        </v-tooltip>
                      </v-layout>
                    </v-list-tile-action>
                  </v-flex>
                </v-layout>
              </v-list-tile>
            </v-list>
          </v-card>
        </v-flex>
        <v-flex xs 12 sm5>
          <v-layout row wrap
          width="100%">
            <v-card-title>
              <div>
                <div class="headline">Cart Summary</div>
                <div>Total Amount: SGD {{amtTotal}}</div>
                <div class="success--text">Less Discount: SGD {{discountTotal}}</div>
                <div 
                v-if="this.couponDiscount !== 0"
                class="success--text">
                  Less Coupon Discount: SGD {{couponDiscount}}
                </div>
                <b>Net Amount Due: SGD {{amtNet}}</b>
                <div>
                  <v-text-field
                    class="mt-4"
                    label="Coupon Code"
                    hint="Your 6-8 digits coupon code"
                    v-model="coupon"
                    persistent-hint
                    color="success"
                    outline
                    :append-icon="coupon ? 'send' : ''"
                    @click:append="couponApply"
                  ></v-text-field>
                </div>
                <div
                class="mt-2 ml-0">
                  <v-btn
                  color="success"
                  @click="checkout">
                    Check Out
                  </v-btn>
                </div>
              </div>
            </v-card-title>
          </v-layout>
        </v-flex>
      </v-layout>
    </v-flex>
    
    <!-- confirm remove dialog -->
    <v-dialog
      v-model="dialogDelete"
      width="400"
    >
      <v-card>
        <v-card-title
          class="headline red lighten-3"
          primary-title
        >
          Confirm Remove {{itemName}}?
        </v-card-title>
        <v-card-text>
          Close this dialog to return to shopping cart if this was a mistake.
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="accent"
            flat
            @click="dialogDelete = false"
          >
            Close
          </v-btn>
          <v-btn
            color="error"
            flat
            @click="confirmRemove(itemId)"
          >
            Confirm
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog
    v-model="checkoutDialog"
    width="400">
      <v-card>
        <v-btn 
        icon @click="checkoutDialog = false">
          <v-icon>close</v-icon>
        </v-btn>
        <v-card-title
        primary-title
        class="headline">
          Shopping Cart Checkout
        </v-card-title>
        <v-card-text>
          Please enter and confirm the following delivery details:
          <v-layout row wrap>
            <v-flex xs12>
              <v-text-field
                v-model="firstName"
                :rules="[rules.name]"
                color="success"
                label="Receiver's First Name"
                maxlength="50"
                counter
                required
              ></v-text-field>
            </v-flex>            
            <v-flex xs12>
              <v-text-field
                v-model="lastName"
                :rules="[rules.name]"
                color="success"
                label="Receiver's Last Name"
                maxlength="50"
                counter
                required
              ></v-text-field>
            </v-flex>
            <v-flex xs12>
              <v-text-field
                v-model="phoneNumber"
                :rules="[rules.phone]"
                color="success"
                label="Your phone number"
                type="number"
                maxlength=8
                counter
                required
              ></v-text-field>
            </v-flex>
            <v-flex xs12>
              <v-text-field
                v-model="email"
                :rules="[rules.email]"
                color="success"
                label="Email address for your order receipt"
                required
              ></v-text-field>
            </v-flex>
          </v-layout>
        </v-card-text>
        <v-divider></v-divider>
        <v-card-actions>
          <v-flex
          class="mr-2">
            <div>
              <div class="headline">Cart Summary</div>
              <div>Total Amount: SGD {{amtTotal}}</div>
              <div class="success--text">Less Discount: SGD {{discountTotal}}</div>
              <div 
              v-if="this.couponDiscount !== 0"
              class="success--text">Less Coupon Discount: SGD {{couponDiscount}}</div>
              <b>Net Amount Due: SGD {{amtNet}}</b>
            </div>
          </v-flex>
          <v-spacer></v-spacer>
          <v-btn          
          color="success"
          :disabled="!checkoutFormIsValid()"
          @click="confirmCheckout">
            Checkout via Paypal
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog
    v-model="redirectDialog"
    persistent=""
    max-width="300">
      <v-card>
        <v-layout
        row wrap
        justify-center
        align-center>
          <v-card-title>
            <v-progress-circular
              :size="50"
              :width="7"
              color="success"
              indeterminate
              class="my-3"
            ></v-progress-circular>
          </v-card-title>
          <v-card-text>
            <h3 class="text-xs-center mb-3 headline">Redirecting you to Paypal's Secured Payment Gateway...</h3>
          </v-card-text>
        </v-layout>
      </v-card>
    </v-dialog>

  </v-container>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  data: () => ({
    buyer: {},
    dialogDelete: false,
    itemId: null,
    itemName: '',
    rules: {
      name: val => (val || '').length > 0 || 'This field is required.',
      phone: val => (val || '').length === 8 || 'An 8 digit local phone number please.',
      email: val => {
        const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
        return pattern.test(val) || 'Invalid e-mail.'
      }
    },
    checkoutDialog: false,
    firstName: '',
    lastName: '',
    phoneNumber: null,
    email: '',
    coupon: '',
    couponReg: '',
    couponRegExp: null,
    couponDiscount: 0,
    // set Blk Party Commission 10%
    fee: 10,
    redirectDialog: false
  }),
  mounted () {
    this.init()
  },
  computed: {
    ...mapGetters({
      users: 'getUsers',
      items: 'getUpdatedItems',
      cartItemIds: 'getCartItems'
    }),
    user () {
      return this.$store.getters.getUser
    },
    vendor () {
      return this.$store.getters.getVendor
    },
    vendors () {
      const vendors = []
      for (let i = 0; i < this.users.length; i++) {
        if (this.users[i].userType === 'vendor') {
          vendors.push(this.users[i])
        }
      }
      return vendors
    },
    buyerAuthenticated () {
      return this.buyer !== {}
    },
    cartItems () {
      let cartItems = []
      function getItemId (item) {
        var itemId = item.itemId
        return itemId
      }
      let itemIdArr = this.cartItemIds.map(getItemId)
      for (var i = 0; i < this.items.length; i++) {
        let itemId = this.items[i].itemId
        if (itemIdArr.indexOf(itemId) !== -1) {
          cartItems.push(this.items[i])
        }
      }
      return cartItems
    },
    cartItemsLoaded () {
      if (this.cartItems.length <= 0) {
        return false
      } else {
        // loop to pass each cart item's qty to cartItems array
        for (let i = 0; i < this.cartItemIds.length; i++) {
          this.getItemQty(this.cartItemIds[i])
        }
        return true
      }
    },
    amtTotal () {
      let amtArr = []
      for (var i = 0; i < this.cartItems.length; i++) {
        let amt = this.cartItems[i].priceSGD * this.cartItems[i].qty
        amtArr.push(amt)
      }
      return Number(amtArr.reduce((a, b) => a + b, 0)).toFixed(2)
    },
    discountTotal () {
      let discountArr = []
      for (var i = 0; i < this.cartItems.length; i++) {
        let discount = this.cartItems[i].discount / 100 * this.cartItems[i].priceSGD * this.cartItems[i].qty
        discountArr.push(discount)
      }
      return Number(discountArr.reduce((a, b) => a + b, 0)).toFixed(2)
    },
    amtNet () {
      if (this.couponDiscount === 0) {
        return (this.amtTotal - this.discountTotal).toFixed(2)
      } else {
        return (this.amtTotal - this.discountTotal - this.couponDiscount).toFixed(2)
      }
    },
    // get error and success msgs
    error () {
      return this.$store.getters.getError
    },
    success () {
      return this.$store.getters.getSuccess
    },
    loading () {
      return this.$store.getters.getLoading
    }
  },
  methods: {
    // initialization functions to be called on mounted lifecycle hook
    init () {
      // load all available items
      this.$store.dispatch('itemsApi')
      this.$store.dispatch('allUsersApi')
      if (this.user) {
        // set data params
        this.buyer.id = this.user.id
        // coupon functions offline for now
        // this.couponReg = this.user.couponReg
        // this.couponRegExp = this.user.couponRegExp
        // this.couponRegRedeem = this.user.couponRegRedeem
        // load all cart items
        this.$store.dispatch('cartItemsApi', {userId: this.buyer.id})
        this.$store.dispatch('setError', {message: 'Only consumers 18 years old and above may purchase and consume alcohol! Please enjoy responsibly.'})
      } else if (this.vendor) {
        // set buyer object params
        this.buyer.id = this.vendor.id
        // coupon functions offline for now
        // this.couponReg = this.user.couponReg
        // this.couponRegExp = this.user.couponRegExp
        // this.couponRegRedeem = this.user.couponRegRedeem
        // load all cart items
        this.$store.dispatch('cartItemsApi', {userId: this.buyer.id})
        this.$store.dispatch('setError', {message: 'Only consumers 18 years old and above may purchase and consume alcohol! Please enjoy responsibly.'})
      }
    },
    getItemQty (payload) {
      for (let i = 0; i < this.cartItems.length; i++) {
        if (this.cartItems[i].itemId === payload.itemId) {
          this.cartItems[i].qty = payload.qty
          break
        }
      }
    },
    removeFromCart (item) {
      this.itemName = item.itemName
      this.itemId = item.itemId
      this.dialogDelete = true
    },
    confirmRemove (itemId) {
      if (this.buyer.id) {
        let payload = {
          userId: this.buyer.id,
          itemId: itemId,
          message: 'Item removed from shopping cart.'
        }
        this.$store.dispatch('removeItemFromCart', payload)
        this.$store.dispatch('cartItemsApi', {userId: this.buyer.id})
        this.dialogDelete = false
      }
    },
    addOneQty (index) {
      let qty = this.cartItems[index].qty
      let payload = {
        userId: this.buyer.id,
        itemId: this.cartItems[index].itemId,
        qty: ++qty
      }
      // update cartItem
      this.$store.dispatch('updateItemInCart', payload)
      // reload all cart items
      this.$store.dispatch('cartItemsApi', {userId: this.buyer.id})
    },
    removeOneQty (index) {
      if (this.cartItems[index].qty > 1) {
        let qty = this.cartItems[index].qty
        let payload = {
          userId: this.buyer.id,
          itemId: this.cartItems[index].itemId,
          qty: --qty
        }
        // update cartItem
        this.$store.dispatch('updateItemInCart', payload)
        // reload all cart items
        this.$store.dispatch('cartItemsApi', {userId: this.buyer.id})
      } else if (this.cartItems[index].qty === 1) {
        this.itemName = this.cartItems[index].itemName
        this.itemId = this.cartItems[index].itemId
        this.dialogDelete = true
      }
    },
    onDismissed () {
      this.$store.dispatch('clearSuccess')
      this.$store.dispatch('clearError')
    },
    checkout () {
      if (this.user) {
        this.email = this.user.email
      } else if (this.vendor) {
        this.email = this.vendor.email
      }
      this.checkoutDialog = true
      this.onDismissed()
    },
    couponApply () {
      // check for valid code
      if (this.coupon !== this.couponReg) {
        this.$store.dispatch('setError', {message: 'Invalid coupon code.'})
      } else if (this.couponRegRedeem !== undefined || '') {
        this.$store.dispatch('setError', {message: 'Coupon code redeemed on ' + this.couponRegRedeem})
      } else {
        // check for expiry
        const date = new Date()
        const today = new Date(date.setDate(date.getDate()))
        if (today >= this.couponRegExp) {
          this.$store.dispatch('setError', {message: 'Coupon code expired.'})
        } else {
          // apply $20 discount
          this.$store.dispatch('setSuccess', {message: 'Coupon code applied!'})
          this.couponDiscount = 20
        }
      }
    },
    checkoutFormIsValid () {
      return (
        this.firstName &&
        this.lastName &&
        this.phoneNumber &&
        this.email
      )
    },
    confirmCheckout () {
      let orderItems = {}
      // loop through all cartItems
      for (let i = 0; i < this.cartItems.length; i++) {
        // fill items object
        orderItems[i] = this.cartItems[i]
      }
      // setup order object to be stored in Firebase
      let order = {
        userId: this.buyer.id,
        deliveryEmail: this.email,
        firstName: this.firstName,
        lastName: this.lastName,
        phoneNumber: this.phoneNumber,
        totalSales: this.amtTotal,
        discountTotal: this.discountTotal,
        couponDiscount: this.couponDiscount,
        netSales: this.amtNet,
        orderItems: orderItems,
        paid: false
      }
      // dispatch order to firebase and create orderId
      this.$store.dispatch('buyerOrderApi', order).then((key) => {
        // inform each vendor of new invoice(s)
        this.vendorInvoice(key)
        // call payment redirect function and pass key as orderId param
        this.paymentRedirect(key)
      })
      // clear coupon discount
      this.couponDiscount = 0
      // close checkoutDialog
      this.checkoutDialog = false
    },
    paymentRedirect (key) {
      // open redirectDialog
      this.redirectDialog = true
      // set orderId param
      const orderId = key
      // redirect to Paypal gateway via Firebase function 'checkout'
      window.location = 'https://us-central1-blk-party.cloudfunctions.net/checkout?price=' + this.amtNet + '&description=' + this.buyer.id + '/' + orderId
    },
    vendorInvoice (key) {
      // set orderId param
      const orderId = key
      // set add days function
      function addDays (date, days) {
        let result = new Date(date)
        result.setDate(date.getDate() + days)
        return result
      }
      // set add hours function
      function addHrs (date, hrs) {
        let result = new Date(date)
        result.setHours(date.getHours() + hrs)
        return result
      }
      // loop through all cartItems
      for (let i = 0; i < this.cartItems.length; i++) {
        // get expected delivery date for each cartItem
        let initDate = addDays(new Date(), this.cartItems[i].deliveryTimeDays)
        let expectedDelivery = addHrs(initDate, this.cartItems[i].deliveryTimeHrs)
        // get vendor name and email
        let vendorId = this.cartItems[i].vendorId
        const vendorName = this.vendors.find(vendor => vendor.userId === vendorId).userName
        const vendorEmail = this.vendors.find(vendor => vendor.userId === vendorId).userEmail
        // set order object
        let invoice = {
          itemId: this.cartItems[i].itemId,
          itemName: this.cartItems[i].itemName,
          vendorId: this.cartItems[i].vendorId,
          vendorName: vendorName,
          vendorEmail: vendorEmail,
          userId: this.buyer.id,
          deliveryEmail: this.email,
          firstName: this.firstName,
          lastName: this.lastName,
          // fee applied to total sales net of applied coupon
          fee: ((this.cartItems[i].discountedPrice * this.cartItems[i].qty) * (this.fee / 100)).toFixed(2),
          discountAmt: this.cartItems[i].discountAmt,
          couponDiscountAmt: this.couponDiscount,
          couponRegRedeem: new Date(),
          destination: this.address,
          zipCode: this.zipCode,
          phoneNumber: this.phoneNumber,
          dateOrdered: new Date(),
          expectedDelivery: expectedDelivery,
          dateDelivered: 'pending update',
          priceSGD: this.cartItems[i].priceSGD,
          quantity: this.cartItems[i].qty,
          totalSales: (this.cartItems[i].discountedPrice * this.cartItems[i].qty).toFixed(2),
          // fee applied to total sales net of applied coupon
          netSales: ((this.cartItems[i].discountedPrice * this.cartItems[i].qty) * (1 - this.fee / 100)).toFixed(2),
          // annex buyer's orderId
          orderId: orderId
        }
        // send order to db via vuex
        this.$store.dispatch('vendorInvoiceApi', invoice)
      }
    }
  }
}
</script>




