<template>
  <v-container fluid grid-list-xl>
    <v-layout row wrap
    justify-left
    align-center
    class="ml-1 mt-3">
      <v-flex d-flex xs12 sm6>
        <h1>Blk. Party Admin Dashboard</h1>
      </v-flex>
      <v-flex d-flex xs12 sm6>
        <h2 class="success--text"> Next vendor payout in {{daysToPayout}} days</h2>
      </v-flex>
    </v-layout>
    <v-layout 
    row wrap
    justify-center
    align-center
    class="my-3">
      <!-- Widgets-->
      <v-flex d-flex sm3 xs12>
        <widget icon="account_circle" :title="userCount" subTitle= '+68.7% month-on-month' supTitle="Feb '19 Total Registered Users" color="#4CAF50"/>
      </v-flex>
      <v-flex d-flex sm3 xs12>
        <widget icon="attach_money" title="SGD 70,938" subTitle= '+100% month-on-month' supTitle="Feb '19 Fee Revenue" color="#2196F3"/>
      </v-flex>
      <v-flex d-flex sm3 xs12>
        <widget icon="computer" title="2,902" subTitle= '+100% month-on-month' supTitle="Feb '19 Orders" color="#424242"/>
      </v-flex>
      <v-flex d-flex sm3 xs12>
        <widget icon="watch_later" title="45 Lapses" subTitle= '0.05% of total orders' supTitle="Feb '19 Delivery Lapses" color="#FF5252"/>
      </v-flex>
      <!-- Widgets Ends -->
      <!-- Statistics -->
      <v-flex d-flex sm6 xs12
      class="mt-3">
        <bar-chart :chart-data="userData" :options="chartOptions"/>
      </v-flex>
      <v-flex d-flex sm6 xs12
      class="mt-3">
        <bar-chart :chart-data="salesData" :options="chartOptions"/>
      </v-flex>
      <v-flex d-flex sm6 xs12
      class="mt-3">
        <bar-chart :chart-data="orderData" :options="chartOptions"/>
      </v-flex>
      <v-flex d-flex sm6 xs12
      class="mt-3">
        <bar-chart :chart-data="lapseData" :options="chartOptions"/>
      </v-flex>
      <!-- Statistics Ends -->
    </v-layout>
  </v-container>
</template>

<script>
import Widget from '@/shared/widget'
import BarChart from '@/shared/barChart'
import { mapGetters } from 'vuex'

export default {
  components: {
    // set component headers to be called in template
    'bar-chart': BarChart,
    'widget': Widget
  },
  mounted () {
    this.fillUserData()
    this.fillSalesData()
    this.fillOrderData()
    this.fillLapseData()
    this.$store.dispatch('allUsersApi')
  },
  computed: {
    ...mapGetters({
      users: 'getUsers'
    }),
    userCount () {
      let userCount = 0
      this.users.forEach(data => {
        // increment userCount by 1 if user type is not admin
        if (data.userType !== 'admin') {
          userCount += 1
        }
      })
      return userCount
    }
  },
  data () {
    return {
      daysToPayout: 14,
      userData: null,
      salesData: null,
      orderData: null,
      lapseData: null,
      chartOptions: {
        scales: {
          // set dual y axes
          yAxes: [{
            id: 'A',
            position: 'left',
            ticks: {
              beginAtZero: true
            },
            gridLines: {
              display: false
            }
          }, {
            id: 'B',
            position: 'right',
            ticks: {
              beginAtZero: false
            },
            gridLines: {
              display: false
            }
          }],
          xAxes: [{
            ticks: {
              beginAtZero: true
            },
            gridLines: {
              display: false
            }
          }]
        },
        legend: {
          display: true
        },
        tooltips: {
          enabled: true,
          mode: 'single'
        },
        responsive: true,
        maintainAspectRatio: false
      }
    }
  },
  methods: {
    fillUserData () {
      this.userData = {
        labels: ['Jan 2019', 'Feb 2019'],
        datasets: [
          {
            label: 'Mthly Growth (%)',
            yAxisID: 'B',
            borderColor: '#82B1FF',
            pointBackgroundColor: '#82B1FF',
            borderWidth: 5,
            pointBorderColor: '#82B1FF',
            backgroundColor: 'transparent',
            data: [0, 68.7],
            // change user growth to a line chart
            type: 'line'
          }, {
            label: 'Reg. Users',
            yAxisID: 'A',
            borderColor: '#4CAF50',
            pointBackgroundColor: '#4CAF50',
            borderWidth: 5,
            pointBorderColor: '#4CAF50',
            backgroundColor: '#4CAF50',
            data: [1000, 1687]
          }
        ]
      }
    },
    fillSalesData () {
      this.salesData = {
        labels: ['Jan 2019', 'Feb 2019'],
        datasets: [
          {
            label: 'Mthly Growth (%)',
            yAxisID: 'B',
            borderColor: '#424242',
            pointBackgroundColor: '#424242',
            borderWidth: 5,
            pointBorderColor: '#424242',
            backgroundColor: 'transparent',
            data: [0, 100.0],
            // change user growth to a line chart
            type: 'line'
          }, {
            label: 'Fee Rev (SGD)',
            yAxisID: 'A',
            borderColor: '#2196F3',
            pointBackgroundColor: '#2196F3',
            borderWidth: 5,
            pointBorderColor: '#2196F3',
            backgroundColor: '#2196F3',
            data: [35469, 70938]
          }
        ]
      }
    },
    fillOrderData () {
      this.orderData = {
        labels: ['Jan 2019', 'Feb 2019'],
        datasets: [
          {
            label: 'Mthly Growth (%)',
            yAxisID: 'B',
            borderColor: '#82B1FF',
            pointBackgroundColor: '#82B1FF',
            borderWidth: 5,
            pointBorderColor: '#82B1FF',
            backgroundColor: 'transparent',
            data: [0, 100.0],
            // change user growth to a line chart
            type: 'line'
          }, {
            label: 'Orders',
            yAxisID: 'A',
            borderColor: '#424242',
            pointBackgroundColor: '#424242',
            borderWidth: 5,
            pointBorderColor: '#424242',
            backgroundColor: '#424242',
            data: [1451, 2902]
          }
        ]
      }
    },
    fillLapseData () {
      this.lapseData = {
        labels: ['Jan 2019', 'Feb 2019'],
        datasets: [
          {
            label: '% Orders',
            yAxisID: 'B',
            borderColor: '#424242',
            pointBackgroundColor: '#424242',
            borderWidth: 5,
            pointBorderColor: '#424242',
            backgroundColor: 'transparent',
            data: [0.09, 0.05],
            // change user growth to a line chart
            type: 'line'
          }, {
            label: 'Delivery Lapses',
            yAxisID: 'A',
            borderColor: '#FF5252',
            pointBackgroundColor: '#FF5252',
            borderWidth: 5,
            pointBorderColor: '#FF5252',
            backgroundColor: '#FF5252',
            data: [35, 45]
          }
        ]
      }
    }
  }
}
</script>




